import React from 'react';
import {
  BrowserRouter as Router,
  Switch,
  Route
} from "react-router-dom";
import { Home, Portfolio, Contact } from "./pages";
import { Header, Footer } from "./components";

function App() {
  return (
    <>
      <Router>
        <Header />
        <div className="container">
          <Switch>
            <Route path={process.env.PUBLIC_URL + "/", process.env.PUBLIC_URL + "/home"}>
              <Home />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/portfolio"}>
              <Portfolio />
            </Route>
            <Route path={process.env.PUBLIC_URL + "/contact"}>
              <Contact />
            </Route>
          </Switch>
        </div>
        <Footer />
      </Router>
    </>
  );
}

export default App;
